html, body, #root, .App {
  background-color: #ff9677;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Arinoe', sans-serif;
                                                
}

html, body {
  scroll-behavior: smooth;
}

.main-container {
  width: 98%;
  height: 98%;
  margin: 1%;
  border-radius: 15px;
  background-color: #2a3950;
  box-sizing: border-box;
  position: fixed;
}

.main-content {
  height: 100%;
  overflow-y: auto;
  color: white;
  padding: 10px;
}

.circular-navbar {
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 230px;
  height: 230px;
  background-color: transparent;
  border: solid 46px #974063;
  border-radius: 50%;
  transition: .5s;
}
.menu-item {
  font-size: 32px;
  position: absolute;
  color: white;
  text-align: center;
  text-decoration: none;
  /* transform-origin: 20px 160px; */
}

.circular-navbar span {
  
  width: 180px;
  height: 180px;
  border-radius: 50%;
  color: white;
  background-color: #41436a;
}



.menu-item.home {
  transform: translate(-86px, 110px) rotate(40deg);
}

.menu-item.games {
  transform: translate(0, 136px) rotate(0);
}

.menu-item.about {
  transform: translate(86px, 110px) rotate(-40deg);
}


.circular-navbar span a {
  text-align: center;
  color: white;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 64px;
  top: 140px;
  cursor: pointer;
}

.circ {
  position: absolute;
}

